// Fixed Header

.fixed-header {
  .app-header {
    position: fixed;
    width: 100%;
    top: 0;

    .app-header__logo {
      visibility: visible;
    }
  }

  .app-main {
    padding-top: $app-header-height;
  }
  @include media-breakpoint-down(md) {
    .app-main {
      padding-top: 80px;
    }
  }
  &:not(.fixed-sidebar):not(.closed-sidebar) {
    .app-sidebar {
      .app-header__logo {
        visibility: hidden;
      }
    }
  }
}