// GENERIC

$enable-caret: true;
$enable-rounded: true;
$enable-shadows: false;
$enable-gradients: false;
$enable-transitions: true;
$enable-grid-classes: true;
$enable-print-styles: false;

$body-bg: #fff;

// Colors

$white: #fff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$black: #000;

$grays: ();

$grays: map-merge(
                (
                        "100": $gray-100,
                        "200": $gray-200,
                        "300": $gray-300,
                        "400": $gray-400,
                        "500": $gray-500,
                        "600": $gray-600,
                        "700": $gray-700,
                        "800": $gray-800,
                        "900": $gray-900
                ),
                $grays
);

//--#{$color}
$brand: #3f6ad8;
$blue: #78C3FB;
$indigo: #444054;
$purple: #794c8a;
$pink: #f65ca2;
$red: #d92550;
$orange: #fd7e14;
$yellow: #f7b924;
$green: #27C427;
$teal: #16E0BD;
$cyan: #16aaff;
$blue-light: #eeeeee;

$primary: $brand;
$secondary: $gray-600;
$success: $green;
$info: $cyan;
$warning: $yellow;
$danger: $red;
$light: $blue-light;
$dark: $gray-800;
$focus: $indigo;
$alt: $purple;

$primary-cm   : #E00520 !default;
$secondary-cm : #C40018 !default;
$terciary-cm  : #F5B015 !default;
$quaternary-cm: #FF9E03 !default;
$quinary-cm   : #FFF3D9 !default;
$senary-cm    : #59595B !default;
$septenary-cm : #F5F5F5 !default;
$octonary-cm  : #B2B2B2 !default;
$nonary-cm    : #C4C5C7 !default;

$action-cm-orange   : #FF6200 !default;
$action-cm-red      : #A70015 !default;
$action-cm-green-1  : #2C5429 !default;
$action-cm-green-2  : #16A816 !default;
$action-cm-green-3  : #26E226 !default;
$action-cm-green-4  : #5FC045 !default;
$action-cm-cyan-1   : #0099BA !default;
$action-cm-cyan-2   : #16CBF2 !default;

// Pill badge colors

$badge-pill-creada-1: #2C5429 !default;
$badge-pill-creada-2: #2C54292D !default;

$badge-pill-aprovada-1: #16A816 !default;
$badge-pill-aprovada-2: #16A8162D !default;

$badge-pill-otorgada-1: #26E226 !default;
$badge-pill-otorgada-2: #26E2262D !default;

$badge-pill-finalizada-1: #FF9E03 !default;
$badge-pill-finalizada-2: #FF9E032D !default;

$badge-pill-cancelada-1: #A70015 !default;
$badge-pill-cancelada-2: #A700152D !default;

$badge-pill-rechazada-1: #FF6200 !default;
$badge-pill-rechazada-2: #FF62002D !default;

$badge-pill-en-banco-1: #0099BA !default;
$badge-pill-en-banco-2: #0099BA2D !default;

$badge-pill-default-1: #C4C5C7 !default;
$badge-pill-default-2: #C4C5C72D !default;


$theme-colors: ();

$theme-colors: map-merge(
  (
    "primary": $primary,
    "secondary": $secondary,
    "success": $success,
    "info": $info,
    "warning": $warning,
    "danger": $danger,
    "light": $light,
    "dark": $dark,
    "focus": $focus,
    "alternate": $alt,
    "primary-cm"        : $primary-cm,
    "secondary-cm"      : $secondary-cm,
    "terciary-cm"       : $terciary-cm,
    "quaternary-cm"     : $quaternary-cm,
    "quinary-cm"        : $quinary-cm,
    "senary-cm"         : $senary-cm,
    "septenary-cm"      : $septenary-cm,
    "octonary-cm"       : $octonary-cm,
    "nonary-cm"         : $nonary-cm,
    "action-cm-orange"  : $action-cm-orange,
    "action-cm-red"     : $action-cm-red,
    "action-cm-green-1" : $action-cm-green-1,
    "action-cm-green-2" : $action-cm-green-2,
    "action-cm-green-3" : $action-cm-green-3,
    "action-cm-green-4" : $action-cm-green-4,
    "action-cm-cyan-1"  : $action-cm-cyan-1,
    "action-cm-cyan-2"  : $action-cm-cyan-2,
    "teal"              : $teal,
    "warning-cm" : $warning-cm,
    "danger-cm"  : $danger-cm,
    "success-cm" : $success-cm,
    "info-cm"    : $info-cm,
    "badge-pill-creada-1" : $badge-pill-creada-1,
    "badge-pill-creada-2" : $badge-pill-creada-2,
    "badge-pill-aprovada-1" : $badge-pill-aprovada-1,
    "badge-pill-aprovada-2" : $badge-pill-aprovada-2,
    "badge-pill-otorgada-1" : $badge-pill-otorgada-1,
    "badge-pill-otorgada-2" : $badge-pill-otorgada-2,
    "badge-pill-finalizada-1" : $badge-pill-finalizada-1,
    "badge-pill-finalizada-2" : $badge-pill-finalizada-2,
    "badge-pill-cancelada-1" : $badge-pill-cancelada-1,
    "badge-pill-cancelada-2" : $badge-pill-cancelada-2,
    "badge-pill-rechazada-1" : $badge-pill-rechazada-1,
    "badge-pill-rechazada-2" : $badge-pill-rechazada-2,
    "badge-pill-en-banco-1" : $badge-pill-en-banco-1,
    "badge-pill-en-banco-2" : $badge-pill-en-banco-2,
    "badge-pill-default-1" : $badge-pill-default-1,
    "badge-pill-default-2" : $badge-pill-default-2
  ),
  $theme-colors
);

// BOOTSTRAP OVERWRITES

$headings-font-weight: 400;

$font-size-base: 0.88rem;
$font-size-lg: ($font-size-base * 1.25);
$font-size-sm: ($font-size-base * 1.1);
$font-size-xs: ($font-size-base / 1.1);

$btn-transition: color .15s, background-color .15s, border-color .15s, box-shadow .15s;
$btn-focus-width: 0;
$btn-focus-box-shadow: none;

$pagination-focus-box-shadow: none;

$body-color: $gray-700;

$card-cap-bg: $white;
$card-border-color: rgba(darken($primary, 25%), .125);

$dropdown-min-width: 15rem;
$dropdown-link-hover-bg: #e0f3ff;
$dropdown-item-padding-y: .4rem;
$dropdown-padding-y: .65rem;

$box-shadow-default: 0 0.46875rem 2.1875rem rgba(darken($primary, 50%), .03), 0 0.9375rem 1.40625rem rgba(darken($primary, 50%), .03), 0 0.25rem 0.53125rem rgba(darken($primary, 50%), .05), 0 0.125rem 0.1875rem rgba(darken($primary, 50%), .03);

$box-shadow-default-right: 0.46875rem 0 2.1875rem rgba(darken($primary, 50%), .03),
0.9375rem 0 1.40625rem rgba(darken($primary, 50%), .03),
0.25rem 0 0.53125rem rgba(darken($primary, 50%), .05),
0.125rem 0 0.1875rem rgba(darken($primary, 50%), .03);

$box-shadow-default-inverse: 0 -0.46875rem 2.1875rem rgba(darken($primary, 50%), .03), 0 -0.9375rem 1.40625rem rgba(darken($primary, 50%), .03), 0 -0.25rem 0.53125rem rgba(darken($primary, 50%), .05), 0 -0.125rem 0.1875rem rgba(darken($primary, 50%), .03);

$box-shadow-lg-default: 0 0.76875rem 2.4875rem rgba($gray-800, .3), 0 1.3375rem 1.70625rem rgba($gray-800, .3), 0 0.55rem 0.53125rem rgba(darken($gray-800, 50%), .05), 0 0.225rem 0.4375rem rgba($gray-800, .3);

$form-check-input-margin-y: 0.3rem;

$input-group-addon-bg: lighten($dropdown-link-hover-bg, 3%);

// Tables

$table-accent-bg: transparent !default;
$table-hover-bg: #e005200f !default;
$table-active-bg: $table-hover-bg;
$table-border-color: $gray-200;
$table-head-bg: $gray-100;
$table-head-color: $gray-600;
$table-cell-padding: .55rem;
$table-cell-padding-sm: .3rem;

// Popovers

$popover-max-width: 320px;
$popover-box-shadow: $box-shadow-default;
$popover-border-color: $card-border-color;
$popover-header-bg: $white;

// Color Modifiers

$link-color: $gray-700;
$component-active-bg: $primary-cm;
$custom-control-indicator-checked-disabled-bg: rgba($primary, .5);
$form-feedback-valid-color:$green;
$form-feedback-invalid-color: $red;
$progress-bar-bg: $primary;
$input-btn-focus-color: rgba($component-active-bg, .25);
$input-focus-border-color: lighten($component-active-bg, 25%);
$custom-control-indicator-checked-bg: $component-active-bg;
$custom-control-indicator-active-bg: lighten($component-active-bg, 35%);
$custom-checkbox-indicator-indeterminate-bg: $component-active-bg;
$custom-range-thumb-bg: $component-active-bg;
$custom-range-thumb-active-bg: lighten($component-active-bg, 35%);
$dropdown-link-active-bg: $component-active-bg;
$nav-pills-link-active-bg: $component-active-bg;
$pagination-active-bg: $component-active-bg;
$list-group-active-bg: $component-active-bg;

$list-group-border-color: rgba($primary, .08) !default;

// Mixins

@mixin box-shadow-architect($shadow) {
  box-shadow: 0 0.66875rem 2.3875rem rgba($shadow, .03), 0 1.1375rem 1.60625rem rgba($shadow, .03), 0 0.45rem 0.73125rem rgba($shadow, .05), 0 0.325rem 0.3875rem rgba($shadow, .03);
}

// Border

@each $color, $value in $theme-colors {
  .border-#{$color} {
    border-color: $value;
  }
}

